.chronology {
  width: 220px;
  position: relative;
  height: 500px;
  opacity: 0;
  transition: opacity 250ms ease-in-out;
}

.appearOpacity {
  opacity: 1;
}

.chronology > strong,
.chronology > p,
.chronology > div {
  margin: 20px 0px;
}

.chronology > strong {
  font-size: 1.5rem;
}

.chronology > .compCircle,
.chronology > hr,
.chronology > .compContent {
  position: absolute;
}

.chronology > .compCircle {
  top: 5%;
  z-index: 1;
}

.chronology > .compCircle > svg {
  font-size: 40px;
}

.chronology > .arrowLine {
  width: 220px;
  top: 8.5%;
  z-index: 0;
  display: flex;
  position: absolute;
}

.chronology > .arrowLine > hr {
  width: 210px;
}

.chronology > .arrowLine > i {
  position: absolute;
  left: 213px;
  top: -11px;
  font-size: 25px;
}

