.pages {
    position: absolute;
    right: 5%;
    top: 50%;
    transform: translateY(-50%);

}
.pages > div {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.pages h3 {
    font-size: 24px;
    padding: 30px;
    color: white;
}

.pages svg {
    cursor: pointer;
    opacity: 0.5;
    transform: scale(2);
}
.pages svg:hover {
    animation: dot 0.5s ease-in-out infinite alternate;
}

.pages svg.active {
    opacity: 1;
}

@keyframes dot {
    0% {
        transform: scale(2);
    }
    100% {
        transform: scale(4);
    }
}